

import * as db from "~/core/database";

export const update = ({ id, name, siteCode, city, state }) =>
  db.update(
    `
    update sites
    set
      name = coalesce(?, name)
      , siteCode = coalesce(?, siteCode)
      , city  = coalesce(?, city)
      , state = coalesce(?, state)
    where id = ?
  `,
    [name, siteCode, city, state, id]
  );
